import React from 'react';
import AppStore from '../stores/App';
import AppService from '../services/App';
import Invoice from './InvoiceComponent';
import moment from 'moment';
import loader from '../images/loader.gif'

export default class AppComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      invoices: [],
      invoice: null,
      ready: false
    };
    this._onChange = this._onChange.bind(this);
  }

  componentDidMount() {
    this.getInvoice();
    AppStore.addChangeListener(this._onChange);
  }

  componentWillUnmount() {
    AppStore.removeChangeListener(this._onChange);
  }

  _onChange() {
    this.setState(this.getInvoiceState());
  }

  getInvoiceState() {
    return {
      invoices: AppStore.invoice
    };
  }

  getInvoice() {
    var vi = this;
    AppService.getInvoice(function () {
      vi.setState({ready: true});
    });
  }

  print() {
    window.print();
  };

  selectInvoice(invoice) {
    this.setState({invoice: invoice});
  };
  resetInvoice() {
    this.setState({invoice: null});
  };



  render() {
    var invoices = this.state.invoices;
    if(this.state.ready) {
      if (invoices) {
        if (this.state.invoice) {
          return <div>
            <Invoice data={this.state.invoice}/>
            <div className="print-box no-print">
              <button className="button" onClick={this.print.bind(this)}>
                Печать
              </button>
              <button className="button cancel" onClick={this.resetInvoice.bind(this)}>
                Назад
              </button>
            </div>
          </div>
        } else {
          var invoiceRow = [];
          var vi = this;
          invoices.forEach(function (item, key) {
            invoiceRow.push(
              <div className="row" key={key}>
                <div className="cell text-center" style={{width:'100px'}}>
                  №{item.Order.OrderId}
                </div>
                <div className="cell text-center" style={{width:'300px'}}>
                  {item.Order.Customer}
                </div>
                <div className="cell text-center" style={{width:'130px'}}>
                  { moment(item.Order.OrderDate).format('DD MMM YYYY hh:mm') }
                </div>
                <div className="cell text-center" style={{width:'130px'}}>
                  <button className="button" onClick={vi.selectInvoice.bind(vi, item)}>
                    Просмотр
                  </button>
                </div>
              </div>
            )
          });
          return <div>
                    <div className="table">
                      <div className="row">
                        <div className="cell text-center" style={{width:'100px'}}>Номер</div>
                        <div className="cell text-center" style={{width:'300px'}}>Покупатель</div>
                        <div className="cell text-center" style={{width:'130px'}}>Дата</div>
                        <div className="cell text-center" style={{width:'130px'}}></div>
                      </div>
                      {invoiceRow}
                    </div>
                 </div>;
        }
      }
    } else if (this.state.ready && invoices.length === 0) {
      return <h1>Нет данных</h1>
    } else {
      return <div className="text-center" style={{marginTop: '20%'}}>
               <img src={loader} alt="Загрузка"/>
             </div>
    }
  }
}


